import React from "react";
import "./Header.css";
import SearchIcon from "@material-ui/icons/Search";
import LocalGroceryStoreIcon from "@material-ui/icons/LocalGroceryStore";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import NotificationsIcon from "@material-ui/icons/Notifications";
import CardTravelIcon from "@material-ui/icons/CardTravel";
import NotListedLocationIcon from "@material-ui/icons/NotListedLocation";
import ShowChartIcon from "@material-ui/icons/ShowChart";
import GetAppIcon from "@material-ui/icons/GetApp";

function Header() {
  return (
    <header>
      <div className="image_logo">
        <img
          src="https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/flipkart-plus_8d85f4.png"
          alt="Logo"
        />
        <a href="#id">
          <span>Explore </span>
          <span id="exp">Plus </span>
          <img
            className="plus_logo"
            src="https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/plus_aef861.png"
            alt="plus"
          />
        </a>
      </div>
      <div className="input_sec">
        <input type="text" placeholder="Search for products, brands and more" />
        <SearchIcon className="header_icon" />
      </div>
      <button type="submit" className="login_btn">
        Login
      </button>
      <span className="header_more">
        More
        <ArrowDropDownIcon />
        <ul className="list_more">
          <li className="li_icon">
            <NotificationsIcon className="li_icons" />
            Notification Preferences
          </li>
          <li className="li_icon">
            <CardTravelIcon className="li_icons" />
            Sell on Flipkart
          </li>
          <li className="li_icon">
            <NotListedLocationIcon className="li_icons" />
            24X7 Customer Care
          </li>
          <li className="li_icon">
            <ShowChartIcon className="li_icons" />
            Advertise
          </li>
          <li className="li_icon">
            <GetAppIcon className="li_icons" />
            Download App
          </li>
        </ul>
      </span>
      <div className="cart">
        <LocalGroceryStoreIcon className="header_Bicon" />
        Cart
      </div>
    </header>
  );
}

export default Header;
